@import "~video-react/styles/scss/video-react";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 100%;
  height: 880px;
  display: flex;
  justify-content: center;
  background-color: rgb(228, 228, 228);

  .inner_container {
    width: 95%;
    height: 820px;
    margin: auto;
    background-color: white;
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);


    .column1, .column2, .column3 {
      width: 100%;
      height: 100%;
      margin: auto;
      display: flex;
      align-items: center;
      flex-direction: column;
    }

    .column1 {
      .video {
        width: 95%;
        height: 28%;
        z-index: 2;
      }

      .noticeBoard {
        width: 95%;
        height: 70%;
        border: 2px solid black;
        margin: 0 0 15px 0;
        border-radius: 5px;
      }
    }

    .column2 {
      .productList {
        width: 100%;
        height: 100%;
        // border: 1px solid #ccc;
        background-color: #fafafa;
        margin: 15px 0;
        border-radius: 5px;
        overflow: scroll;
      }
      input {
        background-color: white;
      }
    }

    .column3 {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      article:nth-child(1) {
        background-color: red;
      }
      article:nth-child(2) {
        background-color: #ff921d;
      }
      article:nth-child(3) {
        background-color: #3ec300;
      }
      article:nth-child(4) {
        background-color: #337ca0;
      }

      article {
        // background-color: red;
        padding: 40px;
        width: 95%;
        height: 21%;
        border-radius: 5px;
        color: rgb(244, 242, 242);
        text-align: center;

        h4 {
          font-weight: bold;
        }

        a {
          color: rgb(244, 242, 242);
          font-size: 30px;

          &:hover {
            font-size: 32px;
            transition: 0.2s ease;
          }
        }
      }
    }
  }
}